@import "../Variables.module.css";

.description {
    margin: auto;
    padding-bottom: 20px;
    text-align: center;
    font: inherit;
}

.avatar {
    margin: auto;
    padding-bottom: 20px;
    width: 256px;
    background-color: var(--sixth-color);
    border-radius: 50%;
}

.name {
    font-size: 22px;
    line-height: calc(33/22);
    font-weight: 900;
}

.tag {
    color: var(--seven-color);
    font-size: 18px;
    line-height: calc(33/18);
    font-weight: 600;
}

.location {
    color: var(--seven-color);
    font-size: 18px;
    line-height: calc(33/18);
    font-weight: 500;
}