@import "../Variables.module.css";

.stats {
    display: flex;
    background-color: var(--sixth-color);
    font: inherit;
    font-size: 18px;
    line-height: calc(22/18);
}
.stats li{
    padding: 20px 0;
    border: 1px solid var(--seven-color);
}

.label {
    color: var(--seven-color);
    width: 100px;
    display: block;
    font-weight: 600;
}

.quantity {
    display: block;
    font-weight: 600;
}